<template>
    <div>
        <!---->
        <el-row>
            <el-col :span="3" class="grid">
                <el-input v-model="input" placeholder="id" size="mini"></el-input>
            </el-col>
            <el-col :span="1" class="grid">
                <el-button type="success" icon="el-icon-search" size="mini" @click.prevent="search()"></el-button>
            </el-col>
        </el-row>
        <br>
        <!---->
        <!--  -->
        <el-table
                element-loading-text=""
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                fit
                :data="list" border style="width: 100%" ref="multipleTable" tooltip-effect="dark">
            <!---->
            <!-- <el-table-column type="selection" width="55"></el-table-column> -->
            <!---->
            <!-- <el-table-column type="index" :index="indexMethod"></el-table-column> -->
            <el-table-column prop="id" label="id" sortable></el-table-column>
            <el-table-column prop="title" label="" ></el-table-column>
            <el-table-column prop="link" label="" ></el-table-column>
            <el-table-column prop="date" label="" :formatter="dateFormat" sortable></el-table-column>
            <el-table-column prop="author" label="" ></el-table-column>
            <el-table-column prop="tag" label="" ></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <router-link to="/updateBlog">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click.prevent="updateBlog(scope.$index, scope.row)"></el-button>
                    </router-link>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="danger" icon="el-icon-delete" size="mini" @click.prevent="deleteBlog(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-col :span="1" class="grid">
            <router-link to="/addBlog">
                <el-button type="success" icon="el-icon-circle-plus-outline" size="mini" round></el-button>
            </router-link>
        </el-col>
    </div>
</template>
<script>
    export default {
        created() {
        this.listAllBlog();
    },
    data() {
        return {
            //
            input: 0,
            list: [{id:'1',title:'3211<p>dsa</p>',link:'2',date:'2',author:'3',tag:'4'},{id:'1',title:'1',link:'2',date:'2',author:'3',tag:'4'}] ,// 
        };
    },
    methods: {
        listAllBlog() {

                //  Vue-resource   this.$http 
            this.$http.get("getAllBlogs").then(result => {
                //   $http  result.body 
                console.log(result);
            if (result.status === 200) {
                // 
//                this.list = result.body;
            } else {
                // 
                alert("");
            }
        });
        },
        //
        dateFormat: function(row, column) {
            var date = row[column.property];
            if (date == undefined) {
                return "";
            }
            return this.$moment(date).format("YYYY-MM-DD HH:mm:ss");
        },
        deleteBlog(index, row) {
            this.$http.delete("deleteBlogById/" + row.id).then(result => {
                if (result.body.code === 200) {
                // 
                this.listAllBlog();
            } else {
                alert("");
            }
        });
        },
        search() {
            this.$http.get("getBlogById/" + this.input).then(result => {
            if (result.status === 200) {
                this.list = [];
                this.list.push(result.body);
            } else {
                alert("");
            }
        });
        },
        updateBlog(index, row) {
            this.$router.push({
                path: `/updateBlog/${row.id}`
        });
        }
    }
    };
</script>



