<style>
    .search{
        width: 100%;
        text-align: center;
    }
    .search .iconfangdajing{
        line-height: 40px;
    }
    .top10{
        margin-top:10px;
    }
    .shaixuan_text .el-dialog{
        width: 90%;
    }
    .shaixuan_text .el-dialog__header{
        padding:10px 10px 0 10px;
    }
    .shaixuan_text .el-dialog__body{
        padding:5px 10px;
    }
    .shaixuan_text .el-radio-group{
        margin-bottom: 10px;
    }
    .shaixuan_text .el-radio-button__inner{
        border-left: 1px solid #DCDFE6;
        border-radius:4px;
        margin: 5px 0 0 5px;
    }
    .fenlei_title{
        border-bottom: 1px solid #DCDFE6;
        font-size: 16px;
        line-height: 30px;
        font-weight: bold;
    }

    .book-cover{
        width: 100%;
    }
    .book-title{
        margin: 5px auto;
    }
    .book-desc{
        height: 5.5rem;
        display: -webkit-box;
        overflow: hidden;
        font-size: 0.8rem;
        margin: 5px auto;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        color: #969ba3;
        line-height: 1.4rem;
    }
    .book-list{
        margin-top: 1rem;
    }
    .book-meta{
        font-size: 0.6rem;
        color: #969ba3;
    }
    .gray {
        color: #969ba3;
    }
    .red {
        color: #ed424b;
    }
    .blue {
        color: #4284ed;
    }
    .tag-small-group em{
        border: 1px solid;
        font-size: 0.3rem;
        margin: 0 0.1rem;
        padding: 0 0.2rem;
    }

</style>
<script>
    export default {
        data() {
        return {
            id: "",
            title: "",
            link: "",
            author: "",
            tag: "",
            wd:"",
            timeout:  null,
            paixu:"1",
            isCollapse: true,
            shaixuan_key: false,
            fenlei:""
        };
    },
    created() {
//        console.log(this.$route);
        this.$http.get("getBlogById/" + this.$route.params.keyword).then(result => {
            //   $http  result.body 
            var result = result.body;
            if (result.code === 200) {
                // 
                this.id = result.data.id;
                this.title = result.data.title;
                this.link = result.data.link;
                this.author = result.data.author;
                this.tag = result.data.tag;
            } else {
                // 
                alert("");
            }
        });
    },
    methods: {
        //
        goMan(){
            this.$router.push({path:'/'})
        },
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        updateBlog() {
            this.$http
                    .put("updateBlog", {
                        id: this.id,
                        title: this.title,
                        link: this.link,
                        author: this.author,
                        tag: this.tag
                    })
                    .then(result => {
                var result = result.body;
                if (result.code === 200) {
                    alert("");
                } else {
                    // 
                    alert("");
                }
            });
        },
        loadAll()
        {
            return [
                {"value": "", "address": "144"},
                {"value": "Hot honey ", "address": "661"},
                {"value": "", "address": "9886113"},
                {"value": "()", "address": "438"}
            ];
        },
        //queryString 
        //cb ,
        querySearchAsync(queryString, cb) {
            var restaurants = this.restaurants;
            var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;

            clearTimeout(this.timeout);
            this.timeout = setTimeout(() => {
                cb(results);
            }, 3000 * Math.random());
        },
        createStateFilter(queryString) {
            return (state) => {
                return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        handleSelect(item) {
            console.log(item);
            this.$router.push({path:'/' +   item.value})
        },
        search() {
            //console.log(ev);
            this.$router.push({path:'/search/' +   this.wd})
            //this.$router.push({path:'/' +   ev.value})
        },
        clickEnter(queryString) {
            console.log(this.wd);
//            this.$router.push({path:'/' +   ev.value})
        }
    },
    mounted() {
        this.restaurants = this.loadAll();
    }
    };
</script>



