import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import '../skin/iconfont/iconfont.js'
import '../skin/iconfont/iconfont.css'
import App from './App.vue'
import VueRouter from 'vue-router'
import router from './router.js'
import VueResource from 'vue-resource'
import Moment from 'moment'

import VueAwesomeSwiper from 'vue-awesome-swiper/dist/vue-awesome-swiper.js'



//  ElementUIui
Vue.use(ElementUI)

//  url
Vue.use(VueRouter)

//  vue-resourceajax
Vue.use(VueResource)


Vue.use(VueAwesomeSwiper)




//  moment  
Vue.prototype.$moment = Moment;//


//    http  url  /   
Vue.http.options.root = 'http://www.abc.com/';

//  emulateJSON :Webapplication/jsonemulateJSON
Vue.http.options.emulateJSON = true;

new Vue({
      el: '#app',
      render: h => h(App),
    //  VM 
    router
})



