<template>
    <div>
        <el-form ref="form" label-width="80px">
            <el-form-item label="id">
                <el-input v-model="id" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="title"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="link"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="author"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="tag" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="0"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="updateBlog"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    export default {
        data() {
        return {
            id: "",
            title: "",
            link: "",
            author: "",
            tag: ""
        };
    },
    created() {

    },
    methods: {
        updateBlog() {
            this.$http
                    .put("updateBlog", {
                        id: this.id,
                        title: this.title,
                        link: this.link,
                        author: this.author,
                        tag: this.tag
                    })
                    .then(result => {
                var result = result.body;
            if (result.code === 200) {
                alert("");
            } else {
                // 
                alert("");
            }
        });
        }
    }
    };
</script>



